import s from "./AerialViewBenefits.module.scss";
import currency from "../../../images/icons/iconCurrency.svg";
import oclock from "../../../images/icons/iconTime.svg";
import resolution from "../../../images/icons/iconResolution.svg";
import coordinates from "../../../images/icons/iconCoordinates.svg";
import { useEffect } from "react";
import { animateHeadline } from "../../common/headlineAnimation";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

export const AerialViewBenefits = () => {
    const benefits = [
        {
            icon: currency,
            title: "  ",
            mainText:
                "          ,       .",
        },
        {
            icon: oclock,
            title: " ",
            mainText:
                "          .",
        },
        {
            icon: resolution,
            title: "   ",
            mainText:
                "         1/,         .",
        },
        {
            icon: coordinates,
            title: "    ",
            mainText:
                " -  ,           ",
        },
    ];
    useEffect(() => {
        const scrollTg = {
            trigger: `.${s.benefits}`,
            start: `-10% center`,
        };
        gsap.registerPlugin(ScrollTrigger);
        animateHeadline(s.headline);
        gsap.fromTo(
            `.${s.benefit}:first-child`,
            {
                y: -150,
                x: -150,
                opacity: 0,
            },
            {
                x: 0,
                y: 0,
                opacity: 1,
                duration: 0.5,
                scrollTrigger: scrollTg,
                onComplete: () => {
                    gsap.to(`.${s.benefit}:first-child`, {
                        x: -200,
                        opacity: 0,
                        scrollTrigger: {
                            trigger: `.${s.benefits}`,
                            start: `80% center`,
                            scrub: 0.7,
                        },
                    });
                },
            }
        );
        gsap.fromTo(
            `.${s.benefit}:nth-child(2)`,
            {
                y: -150,
                x: 150,
                opacity: 0,
                // scale: 0,
            },
            {
                x: 0,
                y: 0,
                opacity: 1,
                // scale: 1,
                duration: 0.5,
                scrollTrigger: scrollTg,
                onComplete: () => {
                    gsap.to(`.${s.benefit}:nth-child(2)`, {
                        x: 200,
                        opacity: 0,
                        scrollTrigger: {
                            trigger: `.${s.benefits}`,
                            start: `80% center`,
                            scrub: 0.7,
                        },
                    });
                },
            }
        );
        gsap.fromTo(
            `.${s.benefit}:nth-child(3)`,
            {
                y: 150,
                x: -150,
                opacity: 0,
                // scale: 0,
            },
            {
                x: 0,
                y: 0,
                opacity: 1,
                // scale: 1,
                duration: 0.5,
                scrollTrigger: scrollTg,
                onComplete: () => {
                    gsap.to(`.${s.benefit}:nth-child(3)`, {
                        x: -200,
                        opacity: 0,
                        scrollTrigger: {
                            trigger: `.${s.benefits}`,
                            start: `80% center`,
                            scrub: 0.7,
                        },
                    });
                },
            }
        );
        gsap.fromTo(
            `.${s.benefit}:last-child`,
            {
                y: 150,
                x: 150,
                opacity: 0,
                // scale: 0,
            },
            {
                x: 0,
                y: 0,
                opacity: 1,
                // scale: 1,
                duration: 0.5,
                scrollTrigger: scrollTg,
                onComplete: () => {
                    gsap.to(`.${s.benefit}:last-child`, {
                        x: 200,
                        opacity: 0,
                        scrollTrigger: {
                            trigger: `.${s.benefits}`,
                            start: `80% center`,
                            scrub: 0.7,
                        },
                    });
                },
            }
        );
    }, []);
    return (
        <div className={`wrapper ${s.benefits__wrapper}`}>
            <h2 className={s.headline}> </h2>
            <div className={s.benefits__block}>
                <p className={s.subtitle}></p>
                <div className={s.benefits}>
                    {benefits.map((e) => {
                        return (
                            <div className={s.benefit}>
                                <div className={s.wrapper__benefit}>
                                    <div className={s.title}>
                                        <img src={e.icon} alt="" />
                                        <h3>{e.title}</h3>
                                    </div>
                                    <p>{e.mainText}</p>
                                </div>
                            </div>
                        );
                    })}
                </div>
            </div>
        </div>
    );
};
