import s from "./News.module.scss";
import spraying from "./../../images/spraying.jpg";
import droneNews from "./../../images/droneNews.jpg";
import droneReason from "./../../images/droneReasone.jpg";
import creating from "./../../images/creating.jpg";
import { Routing } from "../common/Routing/Routing";
import { useEffect } from "react";
import gsap from "gsap";
import { animateHeadline } from "../common/headlineAnimation";
import { ScrollTrigger } from "gsap/ScrollTrigger";

export const News = () => {
    const news = [
        {
            picture: spraying,
            headline: "   ",
            date: "22/04/2019 00:00",
            newsParagraph:
                "         :  ,      ,  , , ,        .",
        },
        {
            picture: droneNews,
            headline: "    ",
            date: "10/04/2019 00:00",
            newsParagraph:
                "           .      Association for Unmanned Vehicle Systems International,             ().",
        },
        {
            picture: droneReason,
            headline: "     ?",
            date: "14/03/2019 00:00",
            newsParagraph:
                "          -        .            ,         . ",
        },
        {
            picture: creating,
            headline: "     ",
            date: "05/03/2019 00:00",
            newsParagraph:
                "        ,             .          .",
        },
    ];
    useEffect(() => {
        gsap.registerPlugin(ScrollTrigger);
        animateHeadline(s.headline);
        gsap.utils.toArray(`.${s.new}`).forEach((e: any) => {
            const img = e.children[0];
            const title = e.children[1].children[0].children[0];
            const date = e.children[1].children[0].children[1];
            const mainInfo = e.children[1].children[1];
            const readMore = e.children[1].children[2].children[0];
            const line = e.children[1].children[2].children[1];
            const scrollTg = {
                trigger: e,
                start: `5% 64%`,
            };
            gsap.fromTo(
                img,
                {
                    opacity: 0,
                    scale: 0,
                    transformOrigin: `center`,
                },
                {
                    opacity: 1,
                    scale: 1,
                    duration: 0.4,
                    scrollTrigger: scrollTg,
                }
            );
            gsap.fromTo(
                title,
                {
                    x: -65,
                    opacity: 0,
                },
                {
                    x: 0,
                    opacity: 1,
                    duration: 0.6,
                    delay: 0.2,
                    scrollTrigger: scrollTg,
                }
            );
            gsap.fromTo(
                date,
                {
                    x: 70,
                    opacity: 0,
                },
                {
                    x: 0,
                    opacity: 1,
                    duration: 0.4,
                    delay: 0.5,
                    scrollTrigger: scrollTg,
                }
            );
            gsap.fromTo(
                mainInfo,
                {
                    y: -50,
                    opacity: 0,
                },
                {
                    y: 0,
                    opacity: 1,
                    duration: 0.5,
                    delay: 0.7,
                    scrollTrigger: scrollTg,
                }
            );
            gsap.fromTo(
                readMore,
                {
                    x: -70,
                    opacity: 0,
                },
                {
                    x: 0,
                    opacity: 1,
                    duration: 0.5,
                    scrollTrigger: {
                        trigger: e,
                        start: `5% center`,
                    },
                }
            );
            gsap.fromTo(
                line,
                {
                    scaleX: 0,
                    transformOrigin: `center`,
                },
                {
                    scaleX: 1,
                    duration: 0.3,
                    delay: 0.5,
                    scrollTrigger: {
                        trigger: e,
                        start: `5% center`,
                    },
                }
            );
        });
        gsap.fromTo(
            `.${s.paging} ul li`,
            {
                y: -50,
                opacity: 0,
            },
            {
                y: 0,
                opacity: 1,
                duration: 0.4,
                stagger: 0.3,
                scrollTrigger: {
                    trigger: `.${s.paging} ul`,
                },
            }
        );
    }, []);
    return (
        <div className={`${s.news__wrapper}`}>
            <div className={s.header__info}>
                <Routing page="" />
                <h2 className={s.headline}>   ?</h2>
            </div>
            <div className={s.news}>
                <p className={s.subtitleTop}> </p>
                {news.map((e) => {
                    return (
                        <div className={s.new}>
                            <span className={s.border}>
                                <img src={e.picture} alt="" />
                            </span>
                            <div className={s.info}>
                                <div className={s.blockTitle}>
                                    <h3>{e.headline}</h3>
                                    <p>{e.date}</p>
                                </div>
                                <p className={s.paragraph}>{e.newsParagraph}</p>
                                <div className={s.readMore}>
                                    <a href="#"> </a>
                                    <span className={s.line}></span>
                                </div>
                            </div>
                        </div>
                    );
                })}
                <p className={s.subtitleBottom}>   </p>
            </div>
            <div className={s.paging}>
                <ul>
                    {[1, 2, 3, 4].map((e, i) => {
                        return <li className={i == 0 ? s.active : ""}>{e}</li>;
                    })}
                </ul>
            </div>
        </div>
    );
};
