import s from "./News.module.scss";
import droneNews from "../../../images/droneNews.jpg";
import droneReasone from "../../../images/droneReasone.jpg";
import creating from "../../../images/creating.jpg";
import { useEffect } from "react";
import { animateHeadline } from "../../common/headlineAnimation";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import gsap from "gsap";

export const News = () => {
    const news = [
        {
            image: droneNews,
            title: "    ",
            content:
                "           .      Association for Unmanned Vehicle Systems International,             ().",
        },
        {
            image: droneReasone,
            title: "     ?",
            content:
                "          -        .            ,         . ",
        },
        {
            image: creating,
            title: "     ",
            content:
                "        ,             .          .",
        },
    ];
    useEffect(() => {
        gsap.registerPlugin(ScrollTrigger);
        animateHeadline(s.headline);
        gsap.fromTo(
            `.${s.news}`,
            {
                opacity: 0,
                x: -200,
            },
            {
                opacity: 1,
                x: 0,
                duration: 0.6,
                stagger: 0.2,
                scrollTrigger: {
                    trigger: `.${s.news__wrapper}`,
                    start: `-40% 100%`,
                    end: `center center`,
                    scrub: 1,
                },
            }
        );
    }, []);
    return (
        <div className={`wrapper ${s.news__wrapper}`}>
            <h2 className={s.headline}>   </h2>
            <div className={s.news__container}>
                {news.map((e) => {
                    return (
                        <div className={s.news}>
                            <div className={s.img__wrapper}>
                                <img src={e.image} alt="" />
                            </div>
                            <h3>{e.title}</h3>
                            <p>{e.content}</p>
                            <span>
                                <a href="" className={s.readMore}>
                                     
                                </a>
                            </span>
                        </div>
                    );
                })}
            </div>
        </div>
    );
};
