import s from "./BenefitsOfSpraying.module.scss";
import wallet from "../../../images/icons/wallet.svg";
import efficiency from "../../../images/icons/efficiency.svg";
import oclock from "../../../images/icons/iconTime.svg";
import check from "../../../images/icons/iconCheck.svg";
import { useEffect } from "react";
import { animateHeadline } from "../../common/headlineAnimation";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

export const BenefitsOfSpraying = () => {
    const benefits = [
        {
            icon: wallet,
            headline: "",
            benefitsList: [
                "    30%",
                "     +5-10%  ",
                "    ",
                "     ,     ",
            ],
        },
        {
            icon: efficiency,
            headline: "",
            benefitsList: [
                "    ",
                "    ,   ,   ,     ",
                "     ",
                "     ",
                "    ",
            ],
        },
        {
            icon: oclock,
            headline: "",
            benefitsList: [
                "     1000    ",
                "  ",
                "     ",
                "     ",
            ],
        },
    ];
    useEffect(() => {
        gsap.registerPlugin(ScrollTrigger);
        const tl = gsap.timeline();
        animateHeadline(s.headline);
        gsap.fromTo(
            `.${s.benefit}`,
            {
                opacity: 0,
                scaleY: 0,
                transformOrigin: `top`,
            },
            {
                opacity: 1,
                scaleY: 1,
                duration: 0.4,
                stagger: 0.3,
                scrollTrigger: {
                    trigger: `.${s.benefits}`,
                    start: `-20% center`,
                },
                onComplete: () => {
                    gsap.to(`.${s.benefit}`, {
                        opacity: 0,
                        y: -150,
                        scrollTrigger: {
                            trigger: `.${s.benefits}`,
                            scrub: 0.3,
                            start: `70% center`,
                        },
                    });
                },
            }
        );
    }, []);
    return (
        <div className={`${s.wrapper} ${s.spraying__wrapper}`}>
            <h2 className={s.headline}> </h2>
            <div className={s.benefits}>
                {benefits.map((e, i) => {
                    return (
                        <div className={s.benefit}>
                            {i == 0 ? (
                                <p className={s.subtitle}></p>
                            ) : (
                                <></>
                            )}
                            <div className={s.benefit__wrapper}>
                                <div className={s.title}>
                                    <img src={e.icon} alt="" />
                                    <h3>{e.headline}</h3>
                                </div>
                                <hr color="#02E502" />
                                <div className={s.content}>
                                    <ul>
                                        {e.benefitsList.map((e) => {
                                            return (
                                                <li>
                                                    <img src={check} alt="" />
                                                    <p>{e}</p>
                                                </li>
                                            );
                                        })}
                                    </ul>
                                </div>
                            </div>
                        </div>
                    );
                })}
            </div>
        </div>
    );
};
